/* eslint-disable no-unreachable */
/* eslint-disable react/display-name */
/* eslint-disable no-unused-vars */
import React, { useEffect, useState, useRef } from 'react';
import { useNavigate } from 'react-router-dom';
import {
    getDocs,
    updateDoc,
    doc,
    addDoc,
    query,
    where,
} from 'firebase/firestore';
import {
    Layout,
    Form,
    PageHeader,
    Input,
    Button,
    message,
    Modal,
    Row,
    Col,
    Table,
    Space,
    Descriptions,
    InputNumber,
    Popover,
    Checkbox,
    Typography,
} from 'antd';
import {
    PlusOutlined,
    MoreOutlined,
    SearchOutlined,
    CloseCircleFilled,
    ExclamationCircleOutlined,
} from '@ant-design/icons';
import moment from 'moment';

import { ROUTE_PATH } from '../../constants';
import styles from './styles.module.scss';

import { recordsRef, usersRef } from '../../services/firebase';

import Logo from '../../assets/images/page_icon.png';
import Leave_Icon from '../../assets/images/leave_icon.png';
import IconSearch from '../../components/IconSearch';

import { useStore } from '../../store';
import {
    SET_CUR_USER,
    SET_CUR_USER_NAME,
    SET_CUR_USER_SERIAL,
} from '../../store/actions';

const UserList = () => {
    const navigate = useNavigate();
    const { dispatch } = useStore();
    const [users, setUsers] = useState([]); // all user 
    const [userSearchedResult, setuserSearchedResult] = useState([]);

    const [isDone, setIsDone] = useState(false);

    const [input, setInput] = useState(''); // 

    useEffect(() => {
        init();
    }, []);

    const init = async () => {
        const users = await fetchUsers();

        console.log(users);

        setUsers(users);
        setuserSearchedResult(users);
        setIsDone(true);
    };

    const fetchUsers = async () => {
        // 
        // const q = query(usersRef, where('isDeleted', '!=', true));
        // const querySnapshot = await getDocs(q);

        const users = [];
        const querySnapshot = await getDocs(usersRef);
        querySnapshot.forEach((doc) => {
            users.push({
                ...doc.data(),
                id: doc.id,
            });
        });

        for (let i = 0; i < users.length; i++) {
            let theUser = users[i];
            console.log(theUser);

            let q = query(recordsRef, where('user', '==', theUser.id));
            const querySnapshot = await getDocs(q);

            const records = [];
            querySnapshot.forEach((doc) => {
                records.push({
                    ...doc.data(),
                    id: doc.id,
                });
            });

            records.sort((a, b) => b.createdTime - a.createdTime);
            theUser.latestRecordTime = records?.[0]?.createdTime;

            console.log(records);
        }

        return users;
    };

    const onSearch = () => {
        if (input.length == 0) {
            setuserSearchedResult(users);
            return;
        }

        // ''.includes('')    true
        const filteredUsers = users.filter((u) => u?.name?.includes(input));

        setuserSearchedResult(filteredUsers);
    };

    const onDeleteUser = (id) => {
        const theUser = users.find((u) => u.id === id);

        Modal.confirm({
            title: `${theUser.name}`,
            icon: <ExclamationCircleOutlined />,
            content: '',
            okText: '',
            okType: 'danger',
            cancelText: '',
            onOk: () => deleteUser(id),
        });
    };

    const deleteUser = async (id) => {
        const theUserRef = doc(usersRef, id);
        await updateDoc(theUserRef, {
            isDeleted: true,
        });

        await fetchUsers();

        message.info('');
    };

    const goDashboard = () => {
        navigate(ROUTE_PATH.admin_dashbaord);
    };

    const goUserDetail = (id, name, idNumber) => {
        if (!id || !name) {
            message.error('');
            return;
        }

        dispatch({
            type: SET_CUR_USER,
            payload: id,
        });
        dispatch({
            type: SET_CUR_USER_NAME,
            payload: name,
        });
        dispatch({
            type: SET_CUR_USER_SERIAL,
            payload: idNumber.slice(0, 4),
        });

        navigate(ROUTE_PATH.user_detail);
    };

    const goEditUser = (id, name) => {
        if (!id || !name) {
            message.error('');
            return;
        }

        dispatch({
            type: SET_CUR_USER,
            payload: id,
        });
        dispatch({
            type: SET_CUR_USER_NAME,
            payload: name,
        });

        navigate(`${ROUTE_PATH.personnel_info}/edit`);
    };

    return (
        <div className={styles.container}>
            <div
                style={{
                    background: `url(${Logo})`,
                    width: 258,
                    height: 56,
                    left: 24,
                    top: 20,
                    position: 'absolute',
                    backgroundSize: '100%',
                    backgroundRepeat: 'no-repeat',
                }}
            />
            <legend></legend>
            <div className={styles.innerContainer}>
                <div
                    onClick={goDashboard}
                    style={{
                        background: `url(${Leave_Icon})`,
                        width: 84,
                        height: 56,
                        right: 24,
                        top: 32,
                        position: 'absolute',
                        backgroundSize: '100%',
                        backgroundRepeat: 'no-repeat',
                    }}
                />
                <div className={styles.cst_btn}></div>
                <div className={styles.inputGroup}>
                    <input onChange={(e) => setInput(e.target.value)} />
                    <div className={styles.iconWrapper}>
                        <IconSearch onClick={onSearch} />
                    </div>
                </div>
                <div className={styles.horiLine} />
                <caption></caption>
                <div className={styles.listWrapper}>
                    {userSearchedResult.map((e, i) => (
                        <section key={e.id}>
                            <span>
                                {moment(e?.latestRecordTime?.toDate()).format(
                                    'YYYY/MM/DD',
                                )}
                            </span>
                            <span className={styles.name}>
                                {e?.name?.length > 4
                                    ? e?.name.substring(0, 3) + '...'
                                    : e?.name}
                            </span>
                            <span>{e?.idNumber?.slice(0, 4)}</span>
                            <span className={styles.situation}>
                                {e?.situation?.length > 7
                                    ? e?.situation.substring(0, 7) + '...'
                                    : e?.situation}
                            </span>
                            {/* <span>5</span> */}
                            <div
                                className={styles.item_btn}
                                onClick={() =>
                                    goUserDetail(e.id, e.name, e.idNumber)
                                }
                            >
                                
                            </div>
                            <div
                                className={styles.item_btn}
                                onClick={() => goEditUser(e.id, e.name)}
                            >
                                
                            </div>
                        </section>
                    ))}
                </div>
            </div>
        </div>
    );
};

export default UserList;
